.table{

    .status{
        padding: 5px;
        border-radius: 5px;

        &.Approved{
            color: green;
            background-color: rgba(0, 128, 0, 0.05);
        }
        &.UnApproved{
            color: crimson;
            background-color: rgba(255, 0, 0, 0.05);
        }
    }
    .nameIcon{
        display: flex;
        align-items: center;
        gap: 10px;
    }
}